    visibility: hidden;

    background-color: rgba(37, 37, 222, 0.653);
    border: 2px solid white;
    border-radius: 5px;

    display: grid;
    grid: auto 1fr 1fr / 600px;
    place-items: center;
    gap: 5px;
}

.weather-focused {
    display: grid;
    grid: 1fr / 200px 400px;
}

.focused-daily-weather {
    width: 200px;

    background-color: rgba(37, 37, 222, 0.1);

    font-size: 0.9rem;

    display:flex;
    flex-direction: column;
    gap: 5px;
    align-items: center;
    justify-content: center;
}

.hourly-weather{
    width: 400px;

    background-color: rgba(37, 37, 222, 0.1);

    font-size: 0.6rem;

    display: flex;
    gap: 15px;
    align-items: center;
    justify-content: start;
}

.daily-weather{
    width: 600px;
    padding: 5px;

    background-color: rgba(37, 37, 222, 0.1);

    font-size: 0.7rem;

    display: flex;
    gap: 25px;
    align-items: center;
    justify-content: center;
}`, "",{"version":3,"sources":["webpack://./src/style/weather-widgets.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;;IAElB,0CAA0C;IAC1C,uBAAuB;IACvB,kBAAkB;;IAElB,aAAa;IACb,0BAA0B;IAC1B,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,YAAY;;IAEZ,wCAAwC;;IAExC,iBAAiB;;IAEjB,YAAY;IACZ,sBAAsB;IACtB,QAAQ;IACR,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,YAAY;;IAEZ,wCAAwC;;IAExC,iBAAiB;;IAEjB,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,sBAAsB;AAC1B;;AAEA;IACI,YAAY;IACZ,YAAY;;IAEZ,wCAAwC;;IAExC,iBAAiB;;IAEjB,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,uBAAuB;AAC3B","sourcesContent":["#weather-container {\n    visibility: hidden;\n\n    background-color: rgba(37, 37, 222, 0.653);\n    border: 2px solid white;\n    border-radius: 5px;\n\n    display: grid;\n    grid: auto 1fr 1fr / 600px;\n    place-items: center;\n    gap: 5px;\n}\n\n.weather-focused {\n    display: grid;\n    grid: 1fr / 200px 400px;\n}\n\n.focused-daily-weather {\n    width: 200px;\n\n    background-color: rgba(37, 37, 222, 0.1);\n\n    font-size: 0.9rem;\n\n    display:flex;\n    flex-direction: column;\n    gap: 5px;\n    align-items: center;\n    justify-content: center;\n}\n\n.hourly-weather{\n    width: 400px;\n\n    background-color: rgba(37, 37, 222, 0.1);\n\n    font-size: 0.6rem;\n\n    display: flex;\n    gap: 15px;\n    align-items: center;\n    justify-content: start;\n}\n\n.daily-weather{\n    width: 600px;\n    padding: 5px;\n\n    background-color: rgba(37, 37, 222, 0.1);\n\n    font-size: 0.7rem;\n\n    display: flex;\n    gap: 25px;\n    align-items: center;\n    justify-content: center;\n}"],"sourceRoot":""}]);
