    font-size: 32px;
}

body {
    position: relative;

    width: 100vw;
    height: 100vh;
    padding: 15px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-position: center;

    text-align: center;

    display: grid;
    grid-template-rows: 200px 1fr;
    grid-template-columns: 1fr;
    place-items: center;
}

button.temperature {
    position: absolute;

    width: 30px;
    height: auto;

    font-size: 0.5rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

button.temperature.c {
    top: 5px;
    left: 5px;
}

button.temperature.f {
    top: 5px;
    left: 35px;
}

img {
    width: auto;
    height: 80px;
}

.loading {
    font-size: 2rem;
    visibility: hidden;
    position: absolute;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

