    padding: 15px;

    align-self: start;
    
    display: grid;
    grid-template-rows: 50px 30px 1fr;
    grid-template-columns: 1fr;
    place-items: center;
    gap: 10px;
}

#city-input {
    width: 450px;
    height: 40px;
    text-align: center;

    font-size: 0.8rem;
    
    border-radius: 5px;
}

#search-btn {
    width: 70px;
    height: 30px;

    font-size: 0.5rem;
    font-weight: bold;

    border-radius: 5px;
